/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - begin
   ========================================================================== */

@include ipad-portrait {

    .woocommerce-page .edgtf-content,
    div.woocommerce {

        .edgtf-quantity-buttons {

            .edgtf-quantity-input,
            .edgtf-quantity-minus,
            .edgtf-quantity-plus {
                font-size: 13px;
            }

            .edgtf-quantity-input {
                height: 38px;
                width: 38px;
                line-height: 36px;
                padding: 0 0 0 4px;
            }

            .edgtf-quantity-minus,
            .edgtf-quantity-plus {
                width: 25px;
                height: 19px;
                line-height: 17px;
            }
        }
    }
}

@include phone-landscape {
    
    .woocommerce-page .edgtf-content,
    div.woocommerce {
        
        .edgtf-quantity-buttons {
            
            .edgtf-quantity-minus,
            .edgtf-quantity-plus {
                width: 19px;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - end
   ========================================================================== */